*,
*::before,
*::after {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
body {
  font-family: "Yaldevi", sans-serif;
}
.weather {
  background-color: lightgrey;
  width: 100%;
  display: flex;
  align-items: center;
  flex-direction: column;
  min-height: 100vh;
}
h1 {
  margin: 3rem;
  background-color: #1f1f1f;
  width: 100%;
  text-align: center;
  color: #fff;
  padding: 1rem 0;
  border-top: 1px solid orange;
  border-bottom: 1px solid orange;
}
form {
  width: 90%;
  display: flex;
}
form input {
  width: 80%;
  padding: 1rem 0.5rem;
  border: 1px solid orange;
}
form button {
  flex-grow: 1;
  margin-left: 0.5rem;
  text-transform: capitalize;
  border: 1px solid orange;
}
.weather-result {
  background-color: #fff;
  width: 90%;
  padding: 1rem;
  margin: 2rem auto;
  border: 0.35rem solid orange;
  border-radius: 0.5rem;
  text-transform: capitalize;
  text-align: center;
}
.weather-result p,
.weather-result img {
  margin: 0.4rem 0;
}
.weather-result p span {
  margin-right: 0.3rem;
}
.louder {
  width: 6rem;
  height: 6rem;
  margin: 2rem auto;
  border: 1rem solid rgba(30, 143, 255, 0.555);
  border-top-color: dodgerblue;
  border-radius: 50%;
  animation: spinner 1s infinite linear;
}
@keyframes spinner {
  fron {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
